<template>
  <div class="main-holder">
    <!-- <md-steppers
      class="steppers-custom"
      md-sync-route
      md-dynamic-height
      md-linear
      :md-changed="changed()"
    >
      <md-step
        v-for="(step, index) in stepsList"
        :key="index"
        :id="step"
        :to="`/checkout/${step}`"
        :md-label="setCurrentLabel(step)"
        :md-error="stepsError[step]"
      > -->
        <router-view />
      <!-- </md-step>
    </md-steppers> -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import authRequestMixin from '../../mixins/authRequestMixin';

export default {
  name: 'Main',
  mixins: [authRequestMixin],
  computed: {
    ...mapGetters(['getCheckoutAddress', 'getAllCartItems']),
  },
  data: () => ({
    stepsList: ['address', 'summary', 'confirmation'],
    steps: {},
    stepsError: {},
  }),
  created() {
    this.initSteps();
  },
  methods: {
    changed() {
      if (this.getAllCartItems.attributes && !this.getAllCartItems.attributes.items.length) {
        this.$nextTick(() => {
          if (!localStorage.getItem('cart_context_token')) {
            if (this.$route.name !== this.stepsList[2]) {
              this.$router.push('/');
              return;
            }
          }

          if (this.$route.name !== this.stepsList[0]) {
            if (!this.getCheckoutAddress.billing) {
              this.$router.push('/checkout');

              this.setError(this.$t('checkout.checkThisStep'), this.stepsList[0]);

              const aList = document.querySelectorAll('a.md-stepper-header');

              aList.forEach((item, index) => {
                if (index) {
                  const numElement = item.querySelector('.md-stepper-number');

                  numElement.textContent = index + 1;
                  item.setAttribute('disabled', 'disabled');
                  item.classList.remove('md-done');
                }
              });
            }
          }

          if (this.$route.name !== this.stepsList[this.stepsList.length - 1]) {
            document
              .querySelectorAll('a.md-stepper-header')[this.stepsList.length - 1]
              .setAttribute('disabled', 'disabled');
          }

          if (this.$route.name === this.stepsList[this.stepsList.length - 1]) {
            document.querySelector('.md-steppers-navigation').style.pointerEvents = 'none';
          }
        });
      }
    },
    initSteps() {
      this.stepsList.forEach((item) => {
        this.steps = { ...this.steps, [item]: false };
        this.stepsError = { ...this.stepsError, [item]: null };
      });
    },
    setDone(id, next) {
      this.steps[id] = true;

      this.removeError();

      if (next && !this.stepsError[this.$route.name]) {
        this.$router.push(`/checkout/${next}`);
      }
    },
    setCurrentLabel(param) {
      return this.$route.name === param ? this.$t(`checkout.${param}`) : '';
    },
    setError(text, step) {
      this.stepsError[step || this.$route.name] = text;
    },
    removeError() {
      this.stepsError[this.$route.name] = null;
    },
  },
};
</script>
